import "./styles.css";

let carrer = document.querySelector(".box");

document.querySelector("#Academic").onclick = () => {
  carrer.classList.toggle("active");
  it.classList.remove("active");
  per.classList.remove("active");
};

let it = document.querySelector(".box-it");

document.querySelector("#Proficiency").onclick = () => {
  it.classList.toggle("active");
  carrer.classList.remove("active");
  per.classList.remove("active");
};

let per = document.querySelector(".box-per");

document.querySelector("#personal").onclick = () => {
  per.classList.toggle("active");
  carrer.classList.remove("active");
  it.classList.remove("active");
};
